@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
  #app{
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

@media (max-width: 1000px){
  #app{
      flex-direction: column;
  }

  #app main{
      margin-left: 0;
      width: 100%;
  }

  #app aside{
      width: 100%;
  }
}
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root, #app{
    height: 100%;
}

body{
    background: #E1E1E1;
}

body, input, button{
    font-family: Roboto, sans-serif;
}
aside{
    width: 320px;
    height: 100vh;
    background: #434343;
    color: #FFF;
    padding-top: 30px;
    text-shadow: 2px 2px black ;
}

aside strong{
    padding: 30px 30px;
    font-size: 34px;
    text-align: left;
    color: #FFF;
}

main{
    flex: 1 1;
    height: 90vh;
    padding: 30px 75px;
}

.content-box span#title{
    font-size: 36px;
}
.menu{
    height: auto;
    margin-top: 30px;
}

.menu-option span{
    font-size: 20px;
}

.menu-option{
    padding: 10px 40px;
    border-style: solid;
    border-width: 1px 0px;
    border-color: #707070;
    display: block;
    color: #FFF;
    text-decoration: none;
}

.menu-option + .menu-option{
    border-top: 0;
}

.menu-option:hover{
    background-color: #303030;
    cursor: pointer;
}
.script-box{
    margin-top: 28px;
    height: 85vh;
    border: 1px solid black;
    background-color: white;
    text-align: center;
}

.script-box input{
    margin-top: 20px;
    padding: 0 2vw;
    width: 95%;
    height: 30px;
    border-radius: 20vh;
    border-color: #adadad;
    border-style: outset;
}

table{
    width: 95%;
    height: 8vh;
    text-align: left;
    margin: 15px 2vw 2vh 2vw;
    border-collapse: collapse;
}

th, td{
    border-bottom: 1px solid #ddd;
    padding: 1vh;
}

tr + tr:hover{
    background-color:#f5f5f5;
}
